import React from 'react'

import {
  HashRouter as Router,
  Switch,
  Route,
  Redirect
} from 'react-router-dom'
import { Link } from 'react-router-dom';

import PageWelcome from "./pages/Welcome";

const ROUTES = [
  {
    path: '/main',
    component: PageWelcome,
    exact:true
  },
  {
  path: '/s',
  isPrivate: true,
  routes: [
    {
      path: '/search',
      component: PageWelcome
    },
  ]
 }
]

const NotFound = () => (
  <div>
    <h1>404 - Not Found!</h1>
    <Link to="/">
      Go Home
    </Link>
  </div>
);

const RouteWithSubRoutes = ({ component: Component, redirect, path, exact, isPrivate, routes, ...params }) => {
  if (redirect) return (<Redirect from={path} to={redirect} />)

  if (isPrivate) {
    return (
      <Route
        path={path}
        exact={exact}
        render={props => {
          // if (AuthService.isAuthenticated) {
          //   return (
          //     <Component
          //       routes={routes}
          //       {...params}
          //       {...props}
          //     />
          //   )
          // }

          return (
            <Redirect to={{
              pathname: '/authorization',
            }}/>
          )
        }}
      />
    )
  }

  return (
    <Route
      path={path}
      exact={exact}
      render={props => (
        <Component
          routes={routes}
          {...params}
          {...props}
        />
      )}
    />
  )
}

class RouteConfig extends React.Component {
  render () {
    return (
      <Router>
        <Switch>
          {
            ROUTES.map((route, i) => (
              <RouteWithSubRoutes
                key={i}
                {...route}
              />
            ))
          }
          <Route component={NotFound} />
        </Switch>
      </Router>
    )
  }
}

export { RouteConfig, RouteWithSubRoutes }
